import { css } from 'lit';

export const styles = css`:host{border:.0625rem solid var(--outline-color);background:var(--background);border-radius:var(--border-radius)}:host([elevated]){-webkit-box-shadow:var(--resting-elevation);box-shadow:var(--resting-elevation)}:host([elevated]:hover),:host([elevated]:focus-within){-webkit-box-shadow:var(--hover-elevation);box-shadow:var(--hover-elevation)}`;
